import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import PostcardList from "../components/PostcardList"
import Pagination from "../components/Pagination"
import HtmlHead from "../components/head"

export default function Home({ data, pageContext }) {
  const siteTitle = data.site.siteMetadata.title
  const author = data.site.siteMetadata.author
  const posts = data.allMarkdownRemark.edges
  const description = data.site.siteMetadata.description
  const categoryTitle = pageContext.category
  const categoryCount = pageContext.count

  return (
    <Layout siteTitle={siteTitle} author={author} metaDescription={description}>
      <h1>
        #{categoryTitle} ({categoryCount})
      </h1>
      <PostcardList posts={posts} />
      <Pagination pageContext={pageContext} />
    </Layout>
  )
}

export const Head = ({ data, pageContext }) => {
  return (< HtmlHead
    siteTitle={data.site.siteMetadata.title}
    pageTitle={pageContext.category}
    metaDescription={pageContext.category}
  />)
}

export const pageQuery = graphql`query ($skip: Int!, $limit: Int!, $category: String) {
  site {
    siteMetadata {
      title
      author
      description
    }
  }
  allMarkdownRemark(
    sort: {frontmatter: {date: DESC}}
    skip: $skip
    limit: $limit
    filter: {frontmatter: {category: {in: [$category]}}}
  ) {
    edges {
      node {
        frontmatter {
          date(formatString: "YYYY.MM.DD")
          update(formatString: "YYYY.MM.DD")
          title
          category
        }
        fields {
          slug
        }
      }
    }
  }
}`
